.nav {
  display: none;

  @include tablet {
    display: block;
    // padding-bottom: 2px;
  }
}

.nav__list {
  @extend %reset_default_styles_list;
  display: grid;
  row-gap: 40px;
  align-items: center;
  justify-items: center;
  font-size: 32px;
  line-height: 160%;

  @include tablet {
    display: grid;
    grid-template-columns: repeat(4, auto);
    column-gap: 36px;
    row-gap: 10px;
    align-items: stretch;
    font-size: 15px;
  }
}

.nav__link {
  display: block;
  box-sizing: border-box;
  text-decoration: none;
  transition-duration: 0.3s;
  padding-bottom: 3px;
  overflow-x: hidden;

  &--current {
    background-size: 100% 3px;
    background-position: left bottom;
    background-repeat: no-repeat;
    background-image: linear-gradient(to bottom, $desert_sand 0%, $desert_sand 100%);

    &:hover {
      transition-duration: 0.2s;
      background-size: 0% 3px;
    }
  }

  &--main-page {
    color: $light_telegray;
  }

  &--main-page-current {
    color: $light_white;
  }

  &--main-page:hover {
    color: $light_white;
  }

  &--our-pets {
    color: $asphalt_color;
  }

  &--our-pets-current {
    color: $signal_black;
  }

  &--our-pets:hover {
    color: $signal_black;
  }
}
